import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronDown } from "@fortawesome/free-solid-svg-icons";

export default class Scroll extends Component {
  constructor() {
    super();
    this.state = {
      transform: null,
      color: "",
      background: "",
    };
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll, { passive: true });
    console.log(window.innerHeight);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  topFunction() {
    window.scroll({ top: 0, behavior: "smooth" });
    window.history.replaceState({}, document.title, "/");
  }

  bottomFunction() {
    window.scroll({ top: window.innerHeight - 25, behavior: "smooth" });
  }

  handleScroll(event) {
    let scrollTop = Math.round(window.scrollY),
      itemTranslate = scrollTop / 3;
    if (itemTranslate <= 179) {
      this.setState({
        transform: itemTranslate,
        color: "#cccccc",
        background: "#282c34",
      });
    } else if (itemTranslate > 180) {
      this.setState({
        transform: 180,
        color: "#cccccc",
        background: "#282c34",
      });
    } else {
      this.setState({ color: "#282c34", background: "#ccc" });
    }
  }

  render() {
    return (
      <div className="scroll-container">
        <div>
          <FontAwesomeIcon
            onScroll={this.handleScroll}
            onClick={
              window.scrollY >= 350 ? this.topFunction : this.bottomFunction
            }
            style={{
              transform: `rotate(${this.state.transform}deg)`,
              color: this.state.color,
              backgroundColor: this.state.background,
              borderColor: this.state.color,
            }}
            icon={faChevronDown}
            className="font-aws font-aws-bot"
          />
        </div>
        {/* <p className="scroll-text">keep scrolling</p> */}
        <p classNAme="scroll-text-back">back to top</p>
      </div>
    );
  }
}
